@import url(https://fonts.googleapis.com/css?family=Rubik:300,400,500,700,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Work+Sans:400,600,700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Rubik', sans-serif;
}

::-webkit-scrollbar {
    width: 0px;  /* Remove scrollbar space */
    background: transparent;  /* Optional: just make scrollbar invisible */
}
/* Optional: show position indicator in red */
::-webkit-scrollbar-thumb {
    background: #ddd;
}

.levelCard {
  border: 4px solid #0F3255;
  background-color: #0F325500;
  color: #ddd;
}

.levelCard:hover {
  background: #ddd !important;
  color: #0F3255 !important;
  cursor: pointer;
}

.backButton {
  width: 110px;
  padding: 0 10px;
  height: 50px;
  border: 4px solid #0F3255;
  background-color: #0F325500;
  border-radius: 50px;
  text-align: center;
  color: #ccc;
  font-size: 16px;
  font-weight: 500;
  text-align: center;
  margin: 0 15px;
  opacity: 0.9;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  cursor: pointer;
}

.backButton:hover {
  border: 4px solid #cad400;
  background-color: #cad400;
  color: #0F3255;
}

.d-flex {
  display: flex;
}

.flex-column {
  flex-direction: column;
}

.justify-content-center {
  justify-content: center;
}

.justify-content-around {
  justify-content: space-around;
}

.justify-content-between {
  justify-content: space-between;
}

.align-items-center {
  align-items: center;
}

.align-self-start {
  align-self: flex-start
}

.align-self-center {
  align-self: center;
}

.align-self-end {
  align-self: flex-end
}

.flex-wrap{
  flex-wrap: wrap
}

.text-start {
  text-align: left
}

.text-center {
  text-align: center;
}

.text-end {
  text-align: right
}

.w-100 {
  width: 100%;
}
